import { Component } from '@angular/core';

import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { FormControl, AbstractControl, FormBuilder, Validators, FormGroup } from '@angular/forms';
import { BikeProvider } from '../../providers/bike/bike';
import { Storage } from '@ionic/storage';
import { MenuController } from 'ionic-angular';
import {MyApp} from '../../app/app.component';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';
/**
 * Generated class for the SellbikeTwoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sellbike-two',
  templateUrl: 'sellbike-two.html',
})
export class SellbikeTwoPage {
  public form: FormGroup;
  myform: FormGroup;
  responseData: any;
  error: string;
  busy: boolean;
  isChecked: boolean;
  disabled: any;

  category:any;
  model:any;
  make:any;
  colour:any;
  engin:any;
  years:any;
  public details : any;


  constructor(private builder: FormBuilder,
    public navCtrl: NavController,
    public alertCtrl: AlertController,
    public navParams: NavParams,
    public menu: MenuController,
    public bikeService: BikeProvider,
    private storage: Storage,
    public loadingCtrl: LoadingController,
    public authService: AuthServiceProvider,
    private myApp:MyApp) {
      this.form = builder.group({
        'make_id': ['', Validators.compose([Validators.required])],
        'year': ['', Validators.compose([Validators.required])],
        'model_id': ['', Validators.compose([Validators.required])],
        'engine_size': ['', Validators.compose([Validators.required])],
        'price': ['', Validators.compose([Validators.required])],
        'color': ['', Validators.compose([Validators.required])],
        'cc': ['', Validators.compose([Validators.required])],
        'category_id': ['', Validators.compose([Validators.required])],
        'fuel_type': ['', Validators.compose([Validators.required])],
        'licence_type': ['', Validators.compose([Validators.required])],
        'no_of_owner': ['',''],
        'description': ['',''],
        'contact_email': ['', Validators.compose([Validators.required])],
        'contact_number': ['', Validators.compose([Validators.required])],
        'is_allow_phone': ['','']
       

      });
      
  }

  ionViewDidLoad() {

    if( localStorage.getItem('bike1_id'))
    {
     let dataUserDet = {
       "id": localStorage.getItem('bike1_id')
       
     };
     this.bikeService.getbikedetails(dataUserDet).subscribe(res=>{
       
       let details = res
       console.log(res)
       if(details.Ack == 1){
         
         this.form.controls['make_id'].setValue(details.product.make_id);
         this.form.controls['year'].setValue(details.product.year);
         this.form.controls['model_id'].setValue(details.product.model_id);
         this.form.controls['engine_size'].setValue(details.product.engine_size);
         this.form.controls['price'].setValue(details.product.price);
         this.form.controls['color'].setValue(details.product.color);
         this.form.controls['cc'].setValue(details.product.cc);
         this.form.controls['category_id'].setValue(details.product.category_id);
         this.form.controls['fuel_type'].setValue(details.product.fuel_type);
         this.form.controls['licence_type'].setValue(details.product.licence_type);
         this.form.controls['no_of_owner'].setValue(details.product.no_of_owner);
         this.form.controls['description'].setValue(details.product.description);
         this.form.controls['contact_email'].setValue(details.product.contact_email);
         this.form.controls['contact_number'].setValue(details.product.contact_number);
         this.form.controls['is_allow_phone'].setValue(details.product.is_allow_phone);
       }
       
      },err=>{
       console.log(err);
        const alert = this.alertCtrl.create({
          title: 'Bike not fetch from your account!',
          buttons: ['OK']
        });
        alert.present();
     });
 
    }





    var year = new Date().getFullYear();
    var range = [];
    range.push(year);
    for (var i = 1; i < 15; i++) {
        range.push(year - i);
    }
    this.years = range;
//console.log('dfuijgudjf')
//     this.bikeService.categories()
//     .subscribe(data => {
//       let details = data;
//      console.log(details);
//       this.categorys = details.categorys;
//   this.models = details.models;
//   this.makes = details.makes;
//   this.colours = details.colours;
//   this.engins = details.engins;
// console.log(this.models)

//     },error => {
//       console.log('error');
//       });

this.authService.getData('products/categories.json').then((result:any) => {
  //console.log(result)
 // this.details = JSON.stringify(result.category);
  //console.log(this.details)
   this.category = result.category;
     this.model =result.model;
     this.make = result.make;
      this.colour = result.colour;
      this.engin = result.engin;
   // console.log(this.engin);
     // console.log(this.category)
}, (err) => {
  console.log(err);
  // Error log
});
    //console.log('ionViewDidLoad SellbikeTwoPage');
  }

  Sellbikethree(fromdata) {
 //  let bike1:any = JSON.parse(localStorage.getItem('bikesell1'));
   if( localStorage.getItem('bike1_id'))
   {
     fromdata.id = localStorage.getItem('bike1_id');
   }
   //console.log(bike1);
    //console.log(fromdata);
    //fromdata.mileage = bike1.mileage;
   // fromdata.reg_number = bike1.reg_number;

    if (!this.form.valid) {
      const alert = this.alertCtrl.create({
        title: 'Bike add faild!',
        subTitle: "Please fill all the details.",
        buttons: ['OK']
      });
      alert.present();
    }
    else{
      let loading = this.loadingCtrl.create({
        spinner: 'show',
        content: 'Loading Please Wait...',
        duration: 3000
      });
      loading.present();
     //console.log(fromdata);
     this.bikeService.addbike(fromdata).subscribe(res=>{
       let details = res
       if(details.ack == 1){
        localStorage.setItem('bike1_id',details.id);
      //   console.log(res);
      //    const alert = this.alertCtrl.create({
      //      title: 'Bike Added Successfully .',
      //      buttons: ['OK']
      //    });
      //  alert.present();
     // this.navCtrl.setRoot('SellbikeThreePage');
      this.navCtrl.push('SellbikeThreePage');
       }
       
      },err=>{
       //console.log(err);
        const alert = this.alertCtrl.create({
          title: 'Service Failed!',
          buttons: ['OK']
        });
        alert.present();
     });
      
    }
    //this.navCtrl.setRoot('SellbikeThreePage');
  }

}



